import {dialogHandle,ajaxErrorLog} from '../dialog';
function zeroSiteDataDayHandle(options){
    return {
        type:"ZEROSITEDATADAY_HANDLE",
        options
    }
}

function zeroSiteDataDayLoad(options){
    return dispatch=>{
        dispatch(zeroSiteDataDayHandle({
            status:"loading"
        }));
        $.XlAjax({
            url:"doublezeronodeDetailReport",
            type:"GET",
            data:options,
            success:function(data){
                if(data.status=="0000"){
                    if(data.data.data.length>0) {
                        dispatch(zeroSiteDataDayHandle({
                            listData: data.data.data,
                            totalSize:data.data.count,
                            status:"success"
                        }));
                    }else{
                        dispatch(zeroSiteDataDayHandle({
                            listData: data.data.data,
                            totalSize:data.data.count,
                            status:"nothing"
                        }));
                    }
                }else{
                    dispatch(zeroSiteDataDayHandle({
                        status:"fail"
                    }));
                }
            },
            error:function(){
                dispatch(zeroSiteDataDayHandle({
                    status:"fail"
                }));
            },
            errorDialog:function(xhr, errorType, error){
                ajaxErrorLog(xhr, errorType, error,dispatch);
            }
        })
    }
}

function zeroSiteDataDayExport(options){
    return dispatch=>{
        $.XlAjax({
            url:"doublezeronodeDetailExportExcel",
            type:"GET",
            data:options,
            success:function(data){
                if(data.status=="0000"){
                    window.location.href=data.data.url;
                }else{
                    dispatch(dialogHandle({
                        show:true,
                        type:'tips',
                        tipsType:'warning',
                        content:data.message
                    }))
                }
            },
            errorDialog:function(xhr, errorType, error){
                ajaxErrorLog(xhr, errorType, error,dispatch);
            }
        })
    }
}

export  {zeroSiteDataDayHandle,zeroSiteDataDayLoad,zeroSiteDataDayExport};